                                  </v-icon>
                              </template>
                              <span>Pilih Karyawan</span>
                            </v-tooltip>

                            <v-dialog
                              v-model="DialogPilihKaryawan"
                              max-width="1000px"
                              scrollable
                              persistent
                            > 
